$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Stepped Content
.wp-block-stp-content {
	* {
		box-sizing: border-box;
	}

	.stpContent {
		width: 100%;
		font-family: 'Roboto', sans-serif;
		overflow: hidden;
	}

	// Navbar
	.stpNav {
		width: 100%;
		height: 65px;
		display: flex;
		align-items: center;
		padding: 5px 15px;
		box-sizing: border-box;
		box-shadow: 0px 1px 2px 0px #3c40434d, 0px 2px 6px 2px #3c404326;

		svg {
			fill: currentColor;
		}

		.menuOpen,
		.menuClose {
			display: none;
			line-height: 100%;
			margin-right: 20px;

			@media screen and (max-width: 768px) {
				display: block;
			}
		}

		h2 {
			font-size: 20px;
			font-weight: 500;
			line-height: 100%;
			margin: 0;
		}

		.remainingTime {
			line-height: 100%;
			display: inline-flex;
			align-items: center;
			gap: 5px;
			margin-left: auto;
		}
	}

	// Section
	.stpSection {
		height: calc(100% - 65px);
		display: flex;

		@media screen and (max-width: 768px) {
			position: relative;
		}
	}

	// Menu
	.stpMenu {
		width: 255px;
		overflow-y: overlay;
		transition: all 0.3s ease-in-out;
		z-index: 10;

		@media screen and (max-width: 768px) {
			position: absolute;
			top: 0;
			left: -255px;
			bottom: 0;
			box-shadow: 2px 0 2px 0 #3c40434d;

			&.active {
				left: 0;
			}
		}

		.numberCount {
			counter-reset: li-count 0;
			list-style-type: none;
			padding: 15px 10px;
			margin: 0px;
			overflow-x: visible;
			overflow-y: auto;

			li {
				counter-increment: li-count 1;

				a {
					min-height: 48px;
					font-size: 14px;
					font-weight: 400;
					// color: rgb(128, 134, 139);
					background-color: white;
					position: relative;
					display: flex;
					align-items: center;
					text-decoration: none;
					padding: 3px 10px;
					border: 1px solid rgb(218, 220, 224);
					border-radius: 5px;
					margin: 6px 0px;
					box-sizing: content-box;
					transition: all 300ms ease-in-out;
					cursor: pointer;

					&:focus {
						outline: none;
					}
				}

				span {
					display: flex;
					align-items: center;

					&::before {
						content: counter(li-count);
						width: 26px;
						height: 26px;
						font-weight: 400;
						font-style: normal;
						line-height: 26px;
						text-align: center;
						vertical-align: middle;
						color: rgb(255, 255, 255);
						background-color: rgb(128, 134, 139);
						display: inline-block;
						border-radius: 50%;
						margin-right: 8px;
						transition: all 300ms ease-in-out 0s;
					}
				}

				&.complete a,
				&.running a {
					// color: #212121;

					span::before {
						color: rgb(255, 255, 255);
						// background-color: rgb(26, 115, 232);
					}
				}

				&.running a {
					font-weight: 600;
					border-color: rgb(255, 255, 255);
					box-shadow: 0 1px 2px 0 #3c40434d, 0 2px 6px 2px #3c404326;

					span::before {
						font-weight: 600;
					}
				}
			}
		}

		.listRange {
			list-style-type: none;
			width: 295px;
			padding: 0;

			li {
				a {
					display: inline-block;
					width: 100%;
					font-size: 15px;
					font-weight: 300;
					line-height: 24px;
					text-decoration: none;
					position: relative;
					padding: 8px 8px 8px 48px;
					border-radius: 5px;
					transition: all 300ms ease-in-out;
					cursor: pointer;

					&::before,
					&::after {
						content: '';
						position: absolute;
						box-sizing: border-box;
					}

					&::before {
						width: 3px;
						height: 100%;
						top: 0;
						left: 24.5px;
						background: #000;
					}

					&::after {
						width: 16px;
						height: 16px;
						top: 13px;
						left: 18px;
						background: #f5f6f9;
						border: 3px solid #000;
						border-radius: 100%;
					}
				}

				&:first-child {
					a {
						&::before {
							background: linear-gradient(#0000 0, #0000 20px, #000 21px, #000);
						}
					}
				}

				&:last-child {
					a {
						&::before {
							background: linear-gradient(#000 0, #000 20px, #0000 21px, #0000);
						}
					}
				}

				&.running {
					a {
						font-weight: 600;
						background-color: #fff;

						&::after {
							background: radial-gradient(circle, #000 0, #000 2px, #f5f5f5 3px, #f5f5f5);
						}
					}
				}

				&.complete {
					a {
						&::after {
							background: #000;
						}
					}
				}
			}
		}
	}

	// Pages
	.stpStepsWrapper {
		position: relative;
		display: flex;
		flex-direction: column;
		flex-grow: 1;

		.stpSteps {
			position: relative;
			flex-grow: 1;
			overflow: hidden;

			.stpStep {
				width: 100%;
				max-width: 100%;
				height: 100%;
				max-height: 100%;
				position: absolute;
				top: 0px;
				right: 0px;
				bottom: 0px;
				left: 0px;
				padding: 30px 10px 0px 10px;
				box-sizing: border-box;
				overflow: hidden;
				transform: translate3d(0px, 0px, 0px);
				transition: all 0.3s ease-in-out;

				&:not(:first-child) {
					transform: translate3d(110%, 0px, 0px);
				}


				&.running {
					transform-origin: 0 50% 0;
					animation-fill-mode: both;
				}
			}

			.instructions {
				font-size: 15px;
				max-width: 800px;
				max-height: calc(100% - 85px);
				background: #fff;
				padding: 24px;
				margin: 0 auto;
				border-radius: 4px;
				box-sizing: border-box;
				overflow-y: auto;
				box-shadow: 0px 1px 2px 0px #3c40434d, 0px 2px 6px 2px #3c404326;
			}

			.instructionTitle {
				font-family: 'Google Sans', Arial, sans-serif;
				font-size: 28px;
				font-weight: 400;
				color: #3c4043;
				margin: 0 0 30px 0;
			}
		}

		.sptStepsControls {
			width: 100%;
			position: absolute;
			bottom: 30px;
			z-index: 10;
			padding: 0px 15px;

			.controlsInner {
				width: 100%;
				max-width: 1025px;
				display: flex;
				align-items: center;
				justify-content: space-between;
				flex-grow: 1;
				margin: 0px auto;
			}

			.docControlBtn {
				max-width: 250px;
				font-family: 'Google Sans', Arial, sans-serif;
				font-size: 14px;
				font-weight: 600;
				line-height: 135%;
				letter-spacing: 0.6px;
				text-decoration: none;
				text-align: left;
				padding: 11px 24px;
				border: none;
				border-radius: 4px;
				outline: 0;
				box-shadow: 0px 2px 2px 0px #00000024, 0px 1px 5px 0px #0000001f, 0px 3px 1px -2px #00000033;
				transition: transform 300ms ease-in-out 0s;
				cursor: pointer;

				&.icon {
					padding: 12px;
				}
			}

			.prevBtn {
				color: #1a73e8;
				background: rgb(255, 255, 255);
				margin-right: auto;

				svg {
					transform: rotate(180deg);
				}
			}

			.nextBtn,
			.doneBtn {
				color: rgb(255, 255, 255);
				margin-left: auto;
			}

			.nextBtn {
				background: rgb(26, 115, 232);
			}

			.doneBtn {
				background: rgb(30, 142, 62);
			}
		}
	}


	// Inner Content
