		line-height: 150%;

		img {
			width: 100%;
			max-width: 100%;
		}

		* {
			color: inherit;
		}

		p {
			margin-bottom: 10px;
		}

		.pre,
		.code {
			font-family: 'Source Code Pro', Helvetica, Arial;
			font-size: inherit;
			border-radius: 4px;

