		.code {
			background-color: #E8EAEd;
			padding: 0.1em 0.3em;

			em {
				color: #97C8F2;
			}
		}

		.pre {
			display: block;
			color: white;
			background-color: #28323f;
			padding: 14px;
			-webkit-text-size-adjust: none;
			line-height: 1.4;
			margin-bottom: 10px;

			code {
				padding: 0;
				background-color: transparent;
			}
		}

		ul.checklist {
			list-style: none;
			padding: 0 0 0 20px;
			margin: 0 0 10px 0;
		}

		ul.checklist li {
			padding-left: 24px;
			background-image: url('data:image/png;base64,');
